<template lang="">
<div class="values">

        <ValueCard
            v-for="(value, i) in getValues"
            :key="i"
            :title="value.title"
            :imageName="value.image"
            :chapters="value.chapters"
        />
</div>
</template>
<script>
import ValueCard from "@/components/utils/CustomCard";
export default {
    name: "DialogValues",

    components: {
        ValueCard,
    },

    props: {
        btncolor: String,
    },

    data() {
        return {
            dialog: false,
            notifications: false,
            sound: true,
            widgets: false,
        };
    },
    computed: {
        getValues() {
            return this.$store.getters.getValues;
        },
    },
};
</script>
