<template>
<div>
    <v-menu offset-y v-model="menuValue">
    <template v-slot:activator="{ on }">
      <v-btn text @click.stop="drawer = !drawer"   v-on="on" :class="{active: menuValue}"><v-icon xLarge>mdi-menu</v-icon></v-btn>
    </template>
  </v-menu>
  
    <v-navigation-drawer
      v-model="drawer"
      absolute     
      temporary
      left
      app
      clipped
      color=secondary
      height=10em>
      
      <v-list
        nav
        dense
      >
        <v-list-item-group
          v-model="group"  
          active-class="highlight"
          color=white>
          
          <v-list-item color="secondary">
              <v-list-item-icon>
              <v-icon>mdi-home</v-icon>
            </v-list-item-icon>
             <v-list-item-title ><router-link to="/" @click.native="setTitle('home')"><h1 class="navigatorLink">{{ $t('navigation.home') }}</h1></router-link></v-list-item-title>
          </v-list-item>

          <v-list-item color="secondary">
              <v-list-item-icon>
              <v-icon>mdi-information-outline</v-icon>
            </v-list-item-icon>
             <v-list-item-title ><router-link to="/about" 
             @click.native="setTitle('about')">
             <h1 class="navigatorLink">{{ $t('navigation.about') }}</h1></router-link></v-list-item-title>
          </v-list-item>

          <v-list-item>
            <v-list-item-icon>
              <v-icon>mdi-hammer</v-icon>
            </v-list-item-icon>
              <v-list-item-title>
                <router-link to="/work" 
                @click.native="setTitle('work')">
                <h1 class="navigatorLink">{{ $t('navigation.work') }}</h1></router-link>
              </v-list-item-title>
          </v-list-item>

          <v-list-item>
             <v-list-item-icon>
              <v-icon>mdi-account-arrow-left</v-icon>
            </v-list-item-icon>
            <v-list-item-title>
              <router-link to="/contact" 
                @click.native="setTitle('contact')">
              <h1 class="navigatorLink">{{ $t('navigation.contact') }}</h1></router-link>
              </v-list-item-title>
          </v-list-item>   
      
          

        </v-list-item-group>
      </v-list>
    </v-navigation-drawer>
    </div>
</template>

<script>

  export default {
    data: () => ({
      menuValue: false,
      drawer: false,
      group: null,
    }),

    watch: {
      group () {
        this.drawer = false
      },
    },

    methods: {
      setTitle(title) {
        this.$store.dispatch("setTitle", title);
      }
    },
 
  }
</script>



<style  scoped>
  .v-btn.active .v-icon {
    transform: rotate(-360deg);
  }
</style>