<template lang="">
    <div>
        <select dark v-model="$i18n.locale" :items="langs" @change="languageChanged($event)">
            <option v-for="(lang, i) in langs" :key="`Lang${i}`" :value="lang">
                <span>{{ lang }}</span>
            </option> 
        </select>
    </div>
</template>
<script>
export default {
    name: "LocalChanger",
    data() {
        return {
            langs: ["de", "en"],
        };
    },

      methods: {
        languageChanged: function(e) {         
            this.$store.dispatch("setLanguage", e.target.options[e.target.options.selectedIndex].value);
        },
    },
};
</script>
<style scoped>
select {
    width: 2em;
    color: white;
}
select option {
    color: black;
}
</style>
